import React, { Component } from 'react'
import  {SmallNav} from '../../../components'
import {getNowPath} from '../function/someFun'
export default class ShowWorksItem extends Component {
    constructor(){
        super()
        this.state={
            smNavdata:{
                navData:[],
                nowData:''
            }
        }
    }
    componentDidMount(){
        this.getRoute()
    }
    getRoute = ()=>{
        const routeArr = getNowPath(this.props.history.location.pathname)
        this.setState({
            smNavdata:{
                navData:routeArr.nav,
                nowData:routeArr.detailRoute,
            }
        })
    }
    render() {
        return (
            <div>
                <SmallNav smNavdata={this.state.smNavdata}  />
                <div className="p-3">
                    <div className="f-content">
                        <div className="myNormalFont " style={{borderBottom:"1px solid black"}}>
                            <h3>
                                XXXX 小程序
                            </h3>
                            
                        </div>
                    </div>
                </div>

              
            </div>
        )
    }
}
